import test from './test'
import { round } from './digit'

// 
function getSys() {
    const sys = uni.getSystemInfoSync()

    let customBar = 0

    // #ifndef MP
    customBar = sys.statusBarHeight + 44
    sys.mpW = {
        bottom: 0,
        top: 0,
        left: 0,
        right: 0,
        width: 0,
        height: 0,
        pad: 10
    }
    // if (sys.platform === 'android') {
    //     customBar = sys.statusBarHeight + 50
    // } else {
    //     customBar = sys.statusBarHeight + 50
    // }
    // #endif

    // #ifdef MP
    const custom = uni.getMenuButtonBoundingClientRect()
    customBar = custom.bottom + custom.top - sys.statusBarHeight
    sys.mpW = custom
    sys.mpW.pad = sys.screenWidth - custom.left - custom.width
    // #endif

    return {
        ...sys,
        customBar
    }
}

// 
function vibrate() {
    // #ifdef APP-PLUS
    if (sys.platform === 'ios') {
        const UIImpactFeedbackGenerator = plus.ios.importClass('UIImpactFeedbackGenerator');
        const impact = new UIImpactFeedbackGenerator()
        impact.prepare()
        impact.init(1)
        impact.impactOccurred()
    }

    if (sys.platform === 'android') {
        uni.vibrateShort()
    }
    // #endif

    // #ifdef MP
    uni.vibrateShort()
    // #endif
}

// toast
function toast(title: string, duration = 2000) {
    uni.showToast({
        icon: 'none',
        title,
        duration
    })
}

// 
function sleep(time = 30) {
    return new Promise(resolve => {
        setTimeout(() => {
            resolve()
        }, time)
    })
}

// 
function trim(str: any, pos = 'both') {
    str = String(str)
    let newStr = ''

    switch (pos) {
        case 'both':
            newStr = str.replace(/^\s+|\s+$/g, '')
            break
        case 'left':
            newStr = str.replace(/^\s*/, '')
            break
        case 'right':
            newStr = str.replace(/(\s*$)/g, '')
            break
        case 'all':
            newStr = str.replace(/\s+/g, '')
            break
        default:
            newStr = str
            break
    }

    return newStr
}

// 
function addStyle(customStyle: any, target = 'object') {
    // 
    if (test.empty(customStyle) || typeof (customStyle) === 'object' && target === 'object' || target === 'string'
        && typeof (customStyle) === 'string') {
        return customStyle
    }
    // 
    if (target === 'object') {
        // (padding: 20px 0)
        customStyle = trim(customStyle)
        // ";"
        const styleArray = customStyle.split(';')
        const style = {}
        // 
        for (let i = 0; i < styleArray.length; i++) {
            // 'font-size:20px;color:red;'";"styleArray
            if (styleArray[i]) {
                const item = styleArray[i].split(':')
                style[trim(item[0])] = trim(item[1])
            }
        }
        return style
    }
    // 
    let string = ''
    for (const i in customStyle) {
        // css
        const key = i.replace(/([A-Z])/g, '-$1').toLowerCase()
        string += `${key}:${customStyle[i]};`
    }
    // 
    return trim(string)
}

function addUnit(value = 'auto', unit = 'px') {
    value = String(value)

    return test.number(value) ? `${value}${unit}` : value
}

// 
function deepClone<T>(obj: T): T {
    // 
    if ([null, undefined, NaN, false].includes(obj)) return obj

    // 
    if (typeof obj !== 'object' && typeof obj !== 'function') return obj

    const o = test.array(obj) ? [] : {}

    for (const i in obj) {
        if (obj.hasOwnProperty(i)) {
            o[i] = typeof obj[i] === 'object' ? deepClone(obj[i]) : obj[i]
        }
    }

    return o
}

// JS
function deepMerge(target = {}, source = {}) {
    target = deepClone(target)
    if (typeof target !== 'object' || typeof source !== 'object') return false
    for (const prop in source) {
        if (!source.hasOwnProperty(prop)) continue
        if (prop in target) {
            if (typeof target[prop] !== 'object') {
                target[prop] = source[prop]
            } else if (typeof source[prop] !== 'object') {
                target[prop] = source[prop]
            } else if (target[prop].concat && source[prop].concat) {
                target[prop] = target[prop].concat(source[prop])
            } else {
                target[prop] = deepMerge(target[prop], source[prop])
            }
        } else {
            target[prop] = source[prop]
        }
    }
    return target
}

// 
function noop(e: EventTarget) {
    e && typeof (e.stopPropagation) === 'function' && e.stopPropagation()
}

// url
function queryParams(data = {}, isPrefix = true, arrayFormat = 'brackets') {
    const prefix = isPrefix ? '?' : ''
    const _result = []
    if (['indices', 'brackets', 'repeat', 'comma'].indexOf(arrayFormat) == -1) arrayFormat = 'brackets'
    for (const key in data) {
        const value = data[key]
        // 
        if (['', undefined, null].indexOf(value) >= 0) {
            continue
        }
        // 
        if (value.constructor === Array) {
            // e.g. {ids: [1, 2, 3]}
            switch (arrayFormat) {
                case 'indices':
                    // : ids[0]=1&ids[1]=2&ids[2]=3
                    for (let i = 0; i < value.length; i++) {
                        _result.push(`${key}[${i}]=${value[i]}`)
                    }
                    break
                case 'brackets':
                    // : ids[]=1&ids[]=2&ids[]=3
                    value.forEach((_value) => {
                        _result.push(`${key}[]=${_value}`)
                    })
                    break
                case 'repeat':
                    // : ids=1&ids=2&ids=3
                    value.forEach((_value) => {
                        _result.push(`${key}=${_value}`)
                    })
                    break
                case 'comma':
                    // : ids=1,2,3
                    let commaStr = ''
                    value.forEach((_value) => {
                        commaStr += (commaStr ? ',' : '') + _value
                    })
                    _result.push(`${key}=${commaStr}`)
                    break
                default:
                    value.forEach((_value) => {
                        _result.push(`${key}[]=${_value}`)
                    })
            }
        } else {
            _result.push(`${key}=${value}`)
        }
    }
    return _result.length ? prefix + _result.join('&') : ''
}

// 
function page() {
    const pages = getCurrentPages()

    return `/${pages[pages.length - 1].route}`
}

// mView
function setConfig({ props = {}, config = {}, zIndex = {} }) {
    uni.$m.config = deepMerge(uni.$m.config, config)
    uni.$m.props = deepMerge(uni.$m.props, props)
    uni.$m.zIndex = deepMerge(uni.$m.zIndex, zIndex)
}

