// 
function priceFormat(number: number, decimals = 0, decimalPoint = '.', thousandsSeparator = ',') {
    thousandsSeparator = trim(thousandsSeparator) || ','
    decimalPoint = trim(decimalPoint) || '.'
    number = (`${number}`).replace(/[^0-9+-Ee.]/g, '')
    const n = !isFinite(+number) ? 0 : +number
    const prec = !isFinite(+decimals) ? 0 : Math.abs(decimals)
    const sep = (typeof thousandsSeparator === 'undefined') ? ',' : thousandsSeparator
    const dec = (typeof decimalPoint === 'undefined') ? '.' : decimalPoint
    let s = ''

    s = (prec ? round(n, prec) + '' : `${Math.round(n)}`).split('.')
    const re = /(-?\d+)(\d{3})/
    while (re.test(s[0])) {
        s[0] = s[0].replace(re, `$1${sep}$2`)
    }

    if ((s[1] || '').length < prec) {
        s[1] = s[1] || ''
        s[1] += new Array(prec - s[1].length + 1).join('0')
    }
    return s.join(dec)
}

// 
function timeFormat(dateTime = null, formatStr = 'yyyy-mm-dd') {
    let date
    // 
    if (!dateTime) {
        date = new Date()
    }
    // unix
    else if (/^\d{10}$/.test(dateTime?.toString().trim())) {
        date = new Date(dateTime * 1000)
    }
    // new Date
    else if (typeof dateTime === 'string' && /^\d+$/.test(dateTime.trim())) {
        date = new Date(Number(dateTime))
    }
    //  RFC 2822 
    else {
        // Safari/Webkitnew Date/
        date = new Date(
            typeof dateTime === 'string'
                ? dateTime.replace(/-/g, '/')
                : dateTime
        )
    }

    const timeSource = {
        'y': date.getFullYear().toString(), // 
        'm': (date.getMonth() + 1).toString().padStart(2, '0'), // 
        'd': date.getDate().toString().padStart(2, '0'), // 
        'h': date.getHours().toString().padStart(2, '0'), // 
        'M': date.getMinutes().toString().padStart(2, '0'), // 
        's': date.getSeconds().toString().padStart(2, '0') // 
        // 
    }

    for (const key in timeSource) {
        const [ret] = new RegExp(`${key}+`).exec(formatStr) || []
        if (ret) {
            // 
            const beginIndex = key === 'y' && ret.length === 2 ? 2 : 0
            formatStr = formatStr.replace(ret, timeSource[key].slice(beginIndex))
        }
    }

    return formatStr
}

// 
function timeFrom(timestamp = null, format = 'yyyy-mm-dd') {
	if (timestamp == null) timestamp = Number(new Date())
	timestamp = parseInt(timestamp)
	// ,js(13),(10)
	if (timestamp.toString().length == 10) timestamp *= 1000
	let timer = (new Date()).getTime() - timestamp
	timer = parseInt(timer / 1000)
	// 5,"",
	let tips = ''
	switch (true) {
		case timer < 300:
			tips = ''
			break
		case timer >= 300 && timer < 3600:
			tips = `${parseInt(timer / 60)}`
			break
		case timer >= 3600 && timer < 86400:
			tips = `${parseInt(timer / 3600)}`
			break
		case timer >= 86400 && timer < 2592000:
			tips = `${parseInt(timer / 86400)}`
			break
		default:
			// formatfalsexx
			if (format === false) {
				if (timer >= 2592000 && timer < 365 * 86400) {
					tips = `${parseInt(timer / (86400 * 30))}`
				} else {
					tips = `${parseInt(timer / (86400 * 365))}`
				}
			} else {
				tips = timeFormat(timestamp, format)
			}
	}
	return tips
}

// uuid
function guid(len = 32, firstU = true, radix = null) {
	const chars = '0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz'.split('')
	const uuid = []
	radix = radix || chars.length

	if (len) {
		// uuid,,0|x,x,
		for (let i = 0; i < len; i++) uuid[i] = chars[0 | Math.random() * radix]
	} else {
		let r
		// rfc4122uuid,
		uuid[8] = uuid[13] = uuid[18] = uuid[23] = '-'
		uuid[14] = '4'

		for (let i = 0; i < 36; i++) {
			if (!uuid[i]) {
				r = 0 | Math.random() * 16
				uuid[i] = chars[(i == 19) ? (r & 0x3) | 0x8 : r]
			}
		}
	}
	// ,u,,guuididclass
	if (firstU) {
		uuid.shift()
		return `m${uuid.join('')}`
	}
	return uuid.join('')
}

