// app
// 

import devConf from './.env.dev'
import testConf from './.env.test'
import prodConf from './.env.prod'
import { App } from 'vue'

const ENV = {
    DEV: 'dev', // 
    TEST: 'test', // 
    PROD: 'prod' // 
}

const envConf = {
    dev: devConf,
    test: testConf,
    prod: prodConf
}

// 
let ENV_KEY = 'dev'

// app
// #ifdef APP-PLUS
ENV_KEY = ENV.DEV
// #endif

// h5 
// #ifdef H5-DEV || WX-DEV
ENV_KEY = ENV.DEV
// #endif

// h5 
// #ifdef H5-TEST || WX-TEST
ENV_KEY = ENV.TEST
// #endif

// h5 
// #ifdef H5-PROD || WX-PROD
ENV_KEY = ENV.PROD
// #endif

// 
const config = envConf[ENV_KEY]

// 
config.version = 'v20220113'

const uniEnv = {}

for (const key in config) uniEnv[key] = config[key]

uni.$env = uniEnv

const install = (app: App) => {
    app.config.globalProperties.$env = uniEnv

    if (['dev', 'test'].indexOf(ENV_KEY) !== -1) {
        uni.$m.toast(`[${ENV_KEY}]: ${uniEnv.version}`)
    }
}

export default {
    install
}
