import request from './requestInterceptors'
import response from './responseInterceptors'
import { App } from 'vue'

uni.$m.http.setConfig(defaultConfig => {
    /* defaultConfig  */
    // defaultConfig.baseURL = process.uniEnv.baseUrl /*  */
    // 
    defaultConfig.getTask = (task, _config) => {
        // 
        uni.$log(task)
        if (!uni.$task) uni.$task = []

        uni.$task.push(task)
    }

    return defaultConfig
})

export default (app: App) => {
    request(app)
    response(app)
}
