<script lang="ts" setup>
import { onPageNotFound, onLaunch, onShow, onHide } from '@dcloudio/uni-app'
import routeIntercept from '@/util/routeIntercept'

onPageNotFound(() => {
    uni.navigateTo({
        url: 'pages/error/404'
    })
})

onLaunch(options => {
    // #ifdef H5
    uni.$NP.done()
    // #endif

    routeIntercept()

    uni.$log('App Launch')
})

onShow(() => {
    uni.$log('App Show')
})

onHide(() => {
    uni.$log('App Hide')
})
</script>

